body{
  background-image: url(Backgound.webp);
  background-size: cover;
}

#signInDiv{
  margin-left: 37%;
  text-align: center;
}

.appDiv{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-left: 2%;
}

.controlDiv{
  display: flex;
  flex-direction: row;
}

h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

input {
  display: inline-block;
  font-size: 30px;
  padding: 10px;
  border: 2px solid #333;
  border-radius: 20px;
  background: none;
}

input:hover {
  border: 5px solid black;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

.headLBL{
  text-align: left;
  font-size: xx-large;
  font-weight: bolder;
}

.errLBL{
  color: red;
  font-size: x-large;
  font-weight: bolder;
}

button {
  font-size: 1rem;
  padding: 10px 20px;
  cursor: pointer;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  transition: background-color 0.2s ease;
}

button:hover {
  background-color: #0056b3;
}

.container {
  display: flex;
  flex-direction: column;
  text-align: left;
  margin-right: 5%;
}

#flightDiv {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  border-radius: 5px;
  max-width: 1500px;
  max-height: 900px;
}

#flightList{
  height: fit-content;
  max-width: 400px;
  max-height: 1000px;
}

#flightDetails{
  margin: 15px;
  border: solid black;
  padding: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

h3 {
  margin-top: 0%;
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  font-weight: bolder;
}

p {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
  font-weight: bolder;
}

h1{
  margin-bottom: 0;
}
